import { ThemeProvider } from "styled-components";
import GlobalStyles from "./styles/GlobalStyles";
import { dark } from './styles/Themes';
import { useRef, useEffect } from 'react';
import Home from "./sections/Home";
import 'locomotive-scroll/dist/locomotive-scroll.css';
import { AnimatePresence } from "framer-motion";
import Project from "./sections/Project";
import "./App.scss";
import { LocomotiveScrollProvider } from 'react-locomotive-scroll';
import ScrollTriggerProxy from "./components/ScrollTriggerProxy";
import { useLocation } from 'react-router-dom';

function App() {
  const containerRef = useRef(null); // 用來引用滾動容器
  const location = useLocation();

  useEffect(() => {
    // 當路徑變更時檢查 hash 值
    if (location.hash === "#project" && containerRef.current) {
      const projectSection = document.querySelector("#project");
      if (projectSection) {
        // 使用 Locomotive Scroll 進行平滑滾動
        containerRef.current.scrollTo({
          top: projectSection.offsetTop,
          behavior: 'smooth'
        });
      }
    }
  }, [location]);

  return (
    <>
      <GlobalStyles />
      <ThemeProvider theme={dark}>
        <LocomotiveScrollProvider
          options={{
            smooth: true, // 啟用平滑滾動
          }}
          watch={[
            location.pathname,  // 監控路徑變更
          ]}
          containerRef={containerRef}
        >
          <ScrollTriggerProxy /> {/* 使用 ScrollTrigger 進行動畫觸發 */}
          <AnimatePresence>
            <main className="App" data-scroll-container ref={containerRef}>
              <Home />  {/* Home 組件 */}
              <Project />  {/* Project 組件 */}
            </main>
          </AnimatePresence>
        </LocomotiveScrollProvider>
      </ThemeProvider>
    </>
  );
}

export default App;
